import React, { Component } from "react";
import { connect } from "react-redux";
import * as actionCreator from "../../redux/actions";
import './Blog.scss';

class Blog extends Component {
  render() {
    return (
      <div className="blog">
        <br />
        <br />
        <br />
        <h1>{this.props.isBg ? "" : "Blog"}</h1>
        <br />
        <br />
        <h2>{this.props.isBg ? "    " : "Still not implemented"}</h2>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    isBg: state.buttons.isBg
  };
};

const mapDispachToProps = dispatch => {
  return {
    onChangeLang: () => dispatch(actionCreator.changeLang())
  };
};
export default connect(
  mapStateToProps,
  mapDispachToProps
)(Blog);
