import React, { Component } from "react";
import { connect } from "react-redux";
import * as actionCreator from "../../redux/actions";
import './Friends.scss';

class Friends extends Component {
  render() {
    return (
      <div className="friends">
        <br />
        <br />
        <br />
        <h1>{this.props.isBg ? " " : "Our friends"}</h1>
        <br />
        <br />
        <h2>{this.props.isBg ? "    " : "Still not implemented"}</h2>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    isBg: state.buttons.isBg
  };
};

const mapDispachToProps = dispatch => {
  return {
    onChangeLang: () => dispatch(actionCreator.changeLang())
  };
};
export default connect(
  mapStateToProps,
  mapDispachToProps
)(Friends);
